	module.exports = {"data":{"markdownRemark":{"html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/2ffd4f417faf2083be8def2a5c6fdbd4-59062.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 250px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Live\"\n        title=\"\"\n        src=\"/static/2ffd4f417faf2083be8def2a5c6fdbd4-57207.png\"\n        srcset=\"/static/2ffd4f417faf2083be8def2a5c6fdbd4-8e8da.png 163w,\n/static/2ffd4f417faf2083be8def2a5c6fdbd4-c2d38.png 325w,\n/static/2ffd4f417faf2083be8def2a5c6fdbd4-57207.png 650w,\n/static/2ffd4f417faf2083be8def2a5c6fdbd4-8e4e2.png 975w,\n/static/2ffd4f417faf2083be8def2a5c6fdbd4-59062.png 1000w\"\n        sizes=\"(max-width: 250px) 100vw, 250px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p><a href=\"http://www.wormieapp.com/\">Wormie</a> is an iOS app to connect you with people in the places you want to see.</p>\n<p><img src=\"/70f4bb033609f915de56b76602148fd0.gif\" alt=\"Live\"></p>\n<p>Maybe you want to see if there's a line at your favorite restaurant right now. Or maybe you want to see tonight's sunset, but from Australia.</p>\n<p><img src=\"/5b786d6755995fd58b55716cd22c89c3.gif\" alt=\"Live\"></p>\n<p>Just open up Wormie, make a new Wormhole request, and Wormie will connect you with someone who can live stream back your request in real-time.</p>\n<p><img src=\"/dc356b2515de8e3e24a9b9b5e518c828.gif\" alt=\"Live\"></p>\n<p>Think TaskRabbit meets Periscope. You can look up specific places with Yelp within Wormie to help you make your Wormhole requests.</p>\n<p><img src=\"/0c968594e3f7338fce7721e74ec8ce78.gif\" alt=\"Live\"></p>\n<p>Design your own Wormie icon, and when you make a Wormhole connection, your Wormies will join together in unique heart!</p>\n<p><img src=\"/dc356b2515de8e3e24a9b9b5e518c828.gif\" alt=\"Live\"></p>\n<h2>The react natives</h2>\n<p>The Wormie team is:</p>\n<ul>\n<li><a href=\"https://github.com/SunyoungKim508\">Sunyoung Kim</a></li>\n<li><a href=\"https://github.com/nickfujita\">Nick Fujita</a></li>\n<li>Me (Charlie Harrington)</li>\n</ul>\n<p>We had a great time building Wormie as a way to learn some new technologies and libraries:</p>\n<ul>\n<li><a href=\"https://facebook.github.io/react-native/\">React Native</a></li>\n<li><a href=\"http://www.django-rest-framework.org/\">Django Rest Framework</a></li>\n<li><a href=\"https://socket.io/\">Socket.io</a></li>\n<li><a href=\"https://webrtc.org/\">WebRTC</a></li>\n<li><a href=\"https://www.docker.com/\">Docker</a></li>\n<li><a href=\"https://developers.google.com/youtube/\">YouTube API</a></li>\n<li><a href=\"http://www.graphicsmagick.org/\">GraphicsMagick</a></li>\n<li><a href=\"https://www.mapbox.com/api-documentation/\">MapBox</a></li>\n<li><a href=\"https://www.yelp.com/developers/documentation/v2/overview\">Yelp API</a></li>\n</ul>","frontmatter":{"date":"January 02, 2016","path":"/space-time-travel-with-wormie","title":"Space Time Travel with Wormie","author":"Charlie Harrington","category":"Projects"}}},"pathContext":{}}

